import {useState, useEffect, useRef} from 'react';
import { useDispatch } from 'react-redux';
import { resetAllCart } from '../../../features/cartSlice';

import { registerLocale, setDefaultLocale } from 'react-datepicker';
import { setHours, setMinutes }from 'date-fns';
import uk from 'date-fns/locale/uk';
import "react-datepicker/dist/react-datepicker.css";
import PhoneInput from 'react-phone-input-2'


import {useSelector} from 'react-redux';

import {Day, Time} from './DataPickers'




const DeliveryForm = ({type, setOrderSuccess})=>{
    const dispatch = useDispatch();

    const [namer, setNamer] = useState(localStorage.getItem('namer') || '');
    

    const refName = useRef(0);
    


    const [phone, setPhone] = useState(localStorage.getItem('phone') || '');
    const [street, setStreet] = useState(localStorage.getItem('street') || '');
    const [house, setHouse] = useState(localStorage.getItem('house') || '');
    const [aprts, setAprts] = useState(localStorage.getItem('aprts') || '');
    const [floor, setFloor] = useState('');
    const [entrance, setEntrance] = useState('');
    const [code, setCode] = useState('');
    const [comments, setComments] = useState('');

    //  ,      ,  -      -    (   )
    const [isAsFast, setIsAsFast] = useState(true);
    //       
    const totalPrice = useSelector(state=>state.cart.totalPriceCart);

    //   ...
    //  -        .             .
    // : Sun Sep 04 2022 15:45:00 GMT+0300 ( ,  )
    //     -
    const [timeUa, setTimeUa] = useState(setHours(setMinutes(new Date(), 0), 10))

  

    //      ,     .
    //    DATETIME   : '2022-09-04T13:59:56.063025+03:00'
    // - ,     Sun Sep 04 2022 15:45:00 GMT+0300 ( ,  )
    //            .    -  .
    //    ,         - (  )
    useEffect(()=>{
        fetch('http://worldtimeapi.org/api/timezone/Europe/Kiev')
        .then(res=>res.json())
        .then(res=>setTimeUa(new Date(res.datetime)));

        //        -
        registerLocale('uk', uk)
        setDefaultLocale('uk')
    }, [isAsFast])
    //   ...

    //clarify -  -       ,  ()
    const [clarify, setClarify] = useState(false);

    //     ,     
    const [payAfter, setPayAfter] = useState(true);
    //    -   
    const [wishCall, setWishCall] = useState(true);

    const downSymbol = clarify ? '' : '';


   

    return (
        <form className="delivery" action="">
            <fieldset className="delivery__contact-fieldset">
            <legend className="delivery__contact-legend"> </legend>

            <div>
            <label className='delivery-label-star' for='namer'>'</label>
            <input 
            ref={refName}
            className="delivery__contact-name" id="namer" name="namer" type="text"  value={namer} onChange={(e)=>{setNamer(e.target.value)}}/>
            </div>
           
           <div className='delivery__contact-phone-wrapper'>
           <label className='delivery-label-star'>.</label>
            <PhoneInput
                inputClass={'delivery__contact-phone'}
                placeholder={''}
                disableDropdown={true}
                country={'ua'}
                countryCodeEditable={false}
                onlyCountries={['ua']}
                value={phone}
                onChange={(phone) => setPhone( phone )}
            />
            </div>
           
           {/*  <input 
             onChange={(e)=>{setPhone(e.target.value)}}
            className="delivery__contact-phone" name="phone" type="text" placeholder=""value={phone} /> */}
            </fieldset>

            <fieldset className="delivery__address-fieldset">
                <legend className="delivery__address-legend"></legend>
                <span className="delivery__city" aria-hidden="true"></span>
              

                <div className='delivery__address-first-wrapper'>
                
                <div>
                <label className='delivery-label-star' for='street'>.</label>
                <input 
                value={street}
                onChange={(e)=>{setStreet(e.target.value)}}
                id='street'
                name="street" type="text" className="delivery__street" />
                </div>
               
                <div>
                <label className='delivery-label-star' for='house'>.</label>
                <input 
                onChange={(e)=>{setHouse(e.target.value)}}
                id='house'
                value={house}
                name="house" type="text" className="delivery__house" />
                </div>

                <div>
                <label for='aprts'>.</label>
                <input 
                id='aprts'
                value={aprts}
                onChange={(e)=>{setAprts(e.target.value)}}
                name="aprts" type="text" className="delivery__flat" placeholder=" / "/>
                </div>
                </div>

                <span 
                onClick={()=>{setClarify(!clarify)}}
                className="clarify"> ', ,  {downSymbol}</span>
                {clarify && <div 
                className='delivery__address-second-wrapper'>
                
                <div> 
                <label for="entrance">'</label>
                <input 
                onChange={(e)=>{setEntrance(e.target.value)}}
                id='entrance'
                name="entrance" type="text" className="delivery__entrance" />
                </div>

                <div>
                <label for="code"></label>
                <input 
                onChange={(e)=>{setCode(e.target.value)}}
                id='code'
                name="code" type="text" className="delivery__house" />
                </div>

                <div>
                <label for="floor"></label>
                <input 
                onChange={(e)=>{setFloor(e.target.value)}}
                id='floor'
                name="floor" type="text" className="delivery__flat" />
                </div>
                </div>}
             

            </fieldset>

            <fieldset className="delivery__data-fieldset">
                <legend className="delivery__data-legend">  </legend>
                <div className="delivery__data-choose-wrapper">
                    {/*      ,     */}
                    <div 
                    onClick={()=>{setIsAsFast(true)}}
                    className='asFast'><span className={isAsFast ? 'custom-radio checked': 'custom-radio'}></span> </div>
                    <div 
                    onClick={()=>{setIsAsFast(false)}}
                    className='onTheData'><span className={isAsFast ? 'custom-radio' : 'custom-radio checked'}></span>   / </div>
                </div>
                {isAsFast 
                ?
                <p style={{marginTop: '15px'}}>*      ,       .</p>
                :
                <div style={{marginTop:'15px', display: 'flex'}}>
                <Time timeUa={timeUa}/> 
                <Day timeUa={timeUa}/>
                </div>
                }
            </fieldset>

            <fieldset className="delivery__pay-fieldset">
                <legend className="delivery__pay-label"></legend>
                <div className="delivery__data-choose-wrapper">
                    {/*      ,     */}
                    <div 
                    onClick={()=>{setPayAfter(true)}}
                    className='asFast'><span className={payAfter ? 'custom-radio checked': 'custom-radio'}></span>  </div>
                    <div 
                    className='onTheData'><span className={payAfter ? 'custom-radio' : 'custom-radio checked'}></span><strike> ONLINE</strike> ( )</div>
                </div>
                {payAfter ?
                    <p style={{marginTop: '15px', display: 'block'}}>*        . <br></br>        ,   -.</p>
                    : null
                    }
            </fieldset>

            <fieldset className="order__add-fieldset">
                <legend className='order__add-label'> </legend>
                <p style={{marginTop: '15px'}}>   ?</p>
                <div className="delivery__data-choose-wrapper">
                    {/*      ,     */}
                <div 
                    onClick={()=>{setWishCall(true)}}
                    className='asFast'><span className={wishCall ? 'custom-radio checked': 'custom-radio'}></span></div>
                <div 
                    onClick={()=>{setWishCall(false)}}
                    className='onTheData'><span className={!wishCall ? 'custom-radio checked': 'custom-radio'}></span></div>
                </div>
                {wishCall ? null : <p className='delivery-note-about-call'>      ,     .</p>}
                <p style={{marginTop: '15px'}}>   </p>
                <textarea
                value={comments}
                onChange={(e)=>{setComments(e.target.value)}}
                className='order__comments' name="comments" placeholder='     ,  ...'></textarea>
            </fieldset>
            <div className="order__form-totalPrice">   {totalPrice} </div>
            {/*   .   ,      -     */}
            {totalPrice == 0 ?
            <div style={{textAlign: 'center', marginTop: '15px'}}>  ,    ...</div>
                :
            <button
            onClick={(e)=>{e.preventDefault();

                            //      
                            if(namer.length === 0 || phone.length === 0 || street.length === 0 || house.length === 0){
                                alert(" ,   '  (', , , ),     .");
                                return
                            }
                            setOrderSuccess('pending');
                            const i = {
                                parse_mode: 'HTML',
                                text: `<b></b>      : ${namer}. \n: ${phone}. 
                                \n : ${type}
                                \n  ${street},  ${house},  ${aprts || '-'},  ${entrance || '-'},  ${code || '-'},  ${floor || '-'}, \n${isAsFast ? '   ' : '   '}, \n${payAfter? '  ' : ' '}, \n${wishCall ? '   ' : '  '}, \n<pre>       : "${comments || '     '}"</pre>`,
                                chat_id: "-666532020",
                    
                                }
                            
                            fetch('https://api.telegram.org/bot5775123731:AAEM6weVVyw5rNROu5H2-_EsU9TtI19utVg/sendMessage', {
                            method: 'POST', 
                            
                                body: JSON.stringify(i), 
                                headers: {
                                'Content-Type': 'application/json'
                                }
                            })
                            .then(res=>res.json())
                            .catch(e=>setOrderSuccess('no'))
                            .then(res=> {
                                if(!res.ok){ 
                                    console.log(res, ' -  ...   ')
                                    setOrderSuccess('no')}
                                else{
                                      //   
                                    localStorage.setItem('phone', phone);
                                    localStorage.setItem('namer', namer);
                                    localStorage.setItem('street', street);
                                    localStorage.setItem('house', house);
                                    localStorage.setItem('aprts', aprts);
                                    dispatch(resetAllCart());
                                    setOrderSuccess('yes')
                                }
                            })

                          
                        }}
            className="order__submit-btn"> </button>
            }
        </form>
    )
}



export default DeliveryForm;